@import url(https://use.fontawesome.com/releases/v5.8.1/css/all.css);
:root {
  --bg-color: #4a5a6c;
  --bg-header: #434753;
  --bg-app: #f2f7f7;
  --bg-message: #54d38a;
  --bg-user-message: #7bc4ef;
  --devider: #e3e7e7;
  --connect: #69c071;
  --disconnect: #f36060;
  --ligth-color: #f7f7f8;
  --scrollbar-thumb: #ced1d6;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  padding: 25px 0;
  background: url(data:image/png;base64,) no-repeat #4a5a6c;
  background: url(data:image/png;base64,) no-repeat var(--bg-color);
}

#root {
  height: 100%;
}

.login {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.App {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  margin: 0 auto;
  background-color: #f2f7f7;
  background-color: var(--bg-app);
  height: 100%;
}

.connect {
  border: 3px solid #69c071;
  border: 3px solid var(--connect);
}

.disconnect {
  border: 3px solid #f36060;
  border: 3px solid var(--disconnect);
}

.user {
  display: flex;
  align-items: center;
  background-color: #434753;
  background-color: var(--bg-header);
  color: #f7f7f8;
  color: var(--ligth-color);
  padding: 15px;
}

.user__avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 1.5rem;
  font-weight: 700;
  margin-right: 10px;
  ;
}

.user-info {
  flex-grow: 1;
}

.user-info__name {
  font-weight: bold;
}

.user-info__connect-status {
  color: #989ca7;
}

.chat {
  flex-grow: 2;
  padding: 20px 10px;
  overflow-y: hidden;
  overflow-x: hidden;
}

.chat-messages {
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 0 10px;
}

.chat-messages:hover {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  -moz-overflow-scrolling: touch;
  -ms-overflow-scrolling: touch;
  -o-overflow-scrolling: touch;
}

.chat-messages::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

.chat-messages::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #ced1d6;
  background-color: var(--scrollbar-thumb);
}

.message {
  display: flex;
  flex-direction: column;
  margin: 0 5px;
}

.message__body {
  position: relative;
  background-color: #54d38a;
  background-color: var(--bg-message);
  color: #fff;
  padding: 15px;
  border-radius: 3px;
  word-wrap: break-word;
  margin-right: 15px;
}

.message_rigth .message__body {
  background-color: #7bc4ef;
  background-color: var(--bg-user-message);
  margin-right: 0;
  margin-left: 15px;
}

.message_rigth p {
  text-align: right;
}

.message__body::after {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  top: -19px;
  border: 10px solid transparent;
  border-bottom: 10px solid #54d38a;
  border-bottom: 10px solid var(--bg-message);
}

.message_rigth .message__body::after {
  border-bottom: 10px solid #7bc4ef;
  border-bottom: 10px solid var(--bg-user-message);
  right: 15px;
}

.message__avtor {
  font-size: 1rem;
  font-weight: 600;
  color: #000;
}

.message__date {
  font-weight: normal;
  font-size: 0.8em;
  color: #c1c5ce;
}

.send-message {
  background-color: #fff;
  padding: 5px 15px;
  border-top: 1px solid #e3e7e7;
  border-top: 1px solid var(--devider);
  min-height: 50px;
}

.send-message__form, .login__form {
  display: flex;
}

.send-message__input, .login__input {
  display: block;
  border: none;
  flex-grow: 1;
  font-size: 1.1rem;
  color: #4a5a6c;
  color: var(--bg-color);
  padding: 10px;
  overflow: hidden;
}

.send-message__input:focus, .login__input:focus, .btn-sing-out:focus, .btn-sing-in:focus, .send-message__submit:focus {
  outline: none;
}

.send-message__input::-webkit-input-placeholder, .login__input::-webkit-input-placeholder {
  color: #d0d6db;
  opacity: 1;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

.send-message__input:focus::-webkit-input-placeholder, .login__input:focus::-webkit-input-placeholder {
  opacity: 0;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

.send-message__submit, .btn-sing-in {
  cursor: pointer;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: bold;
  color: #e3e7e7;
  color: var(--devider);
  border: none;
  background: #fff;
  padding: 8px 14px;
}

.btn-sing-in {
  background: #54d38a;
  background: var(--bg-message);
}

.send-message__submit:hover {
  color: #54d38a;
  color: var(--bg-message);
}

.btn-sing-in:hover {
  background: #69c071;
  background: var(--connect);
}

.btn-sing-out {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 2rem;
  color: #f2f7f7;
  color: var(--bg-app);
}

.btn-sing-out:hover {
  color: #f36060;
  color: var(--disconnect);
}

@media (max-height: 850px) {
  body {
    padding: 0;
  }
}

@media (max-width: 400px) {
  .login__input {
    font-size: 0.9rem;
  }
}

